.bg{
    background-image: url("https://ptspng.in/wp-content/uploads/2023/01/Effects-1-1024x1024.png");
    width: 100%;
    background-size: cover;
}

.h-head{
    color: black;
    font-size: 20px;
    padding-top: 10px;
    font-style: italic;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif;
}
.white{
    color: black;
    font-size: 40px;
}
/* Define the animation */
@keyframes colorChange {
    0% {
        color: black;
    }
    50% {
        color: white;
    }
    100% {
        color: black;
    }
}

.white {
    font-size: 40px;
    animation: colorChange 2s infinite; 
}
.h-image-1{
    width: 80%;
}
.auth{
    text-align:right;
}